<template>
    <div class="wid-100 hei-100">
        <header-bar navTitle=""></header-bar>
        <div class="wid-100 app-content">
          <div class="intro-box">
            <p>6A93185500500...</p>
            <video src="http://www.qpzhangma.com/.mp4" controls muted autoplay class="wid-100" id="introVideo"></video>
          </div>
        </div>
        <div class="loading-box" v-if="isLoading">
          <spinner :color="spinnerColor" :size="50" :depth="4"></spinner>
        </div>
    </div>
</template>

<script>
import Spinner from 'vue-spinner-component/src/Spinner'
import HeaderBar from '@/components/header'
export default {
  components: {
    HeaderBar,
    Spinner
  },
  data () {
    return {
      isLoading: true
    }
  },
  mounted () {
    let _this = this
    let introVideo = document.getElementById('introVideo')
    introVideo.oncanplaythrough = function () {
      _this.isLoading = false
    }
  }
}
</script>

<style scoped>
.intro-box{
  padding: 15px;
}
.intro-box p{
  font-size: 14px;
  line-height: 1.5;
}
</style>



